function Auto(str) {
  var search = new Array(
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',

    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',

    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
  );
  var replace = new Array(
    'q',
    'w',
    'e',
    'r',
    't',
    'y',
    'u',
    'i',
    'o',
    'p',
    '\\[',
    '\\]',

    'a',
    's',
    'd',
    'f',
    'g',
    'h',
    'j',
    'k',
    'l',
    ';',
    "'",

    'z',
    'x',
    'c',
    'v',
    'b',
    'n',
    'm',
    ',',
    '\\.',
  );
  for (var i = 0; i < replace.length; i++) {
    var reg = new RegExp(replace[i], 'mig');

    str = str.replace(reg, function (a) {
      return a == a.toLowerCase() ? search[i] : search[i].toUpperCase();
    });
  }
  return str;
}

export default Auto;
